.header{
    display: flex;
    justify-content: space-between;
}

.logo{
    width:10rem;
    height:3rem
}

.header-menu{
    list-style-type: none;
    display: flex;
    gap: 2rem;
    color: white;
}
.header-menu>li{
    cursor: pointer; 
}
.header-menu>li:hover{
    color: var(--orange);
    cursor: pointer;
}